import React from 'react';
import Amplify, { API } from 'aws-amplify';
import awsconfig from './aws-exports';
import { withAuthenticator } from 'aws-amplify-react';

import logo from './logo.svg';
import './App.css';

Amplify.configure(awsconfig);

class App extends React.Component {
  post = async () => {
    console.log('calling api');
    const response = await API.post('huyItems', '/huyItems', {
      body: {
        id: '1',
        name: 'hello amplify!'
      }
    });
    alert(JSON.stringify(response, null, 2));
  }
  get = async () => {
    console.log('calling api');
    const response = await API.get('huyItems', '/huyItems/object/1');
    alert(JSON.stringify(response, null, 2));
  }
  list = async () => {
    console.log('calling api');
    const response = await API.get('huyItems', '/huyItems/1');
    alert(JSON.stringify(response, null, 2));
  }
  render() {
    return (
      <div className="App">
        <button onClick={this.post}>POST</button>
        <button onClick={this.get}>GET</button>
        <button onClick={this.list}>LIST</button>
      </div>
    );
  }
}

const signUpConfig = {
  header: 'My Customized Sign Up',
  hideAllDefaults: true,
  defaultCountryCode: '84',
  signUpFields: [
    {
      label: 'Email',
      key: 'email',
      required: true,
      displayOrder: 1,
      type: 'string'
    },
    {
      label: 'Password',
      key: 'password',
      required: true,
      displayOrder: 2,
      type: 'password'
    },
    {
      label: 'Name',
      key: 'name',
      required: true,
      displayOrder: 4,
      type: 'string',
    },
    {
      label: 'My user name',
      key: 'username',
      required: true,
      displayOrder: 5,
      type: 'string'
    },
  ]
};

export default withAuthenticator(App, { signUpConfig });
